import React from "react";
import { Link } from "react-router-dom";

export default function header() {
  return (
    <header style={headerStyle}>
      <h2>Tasks</h2>
      <Link className="App-link" to="/">
        Home
      </Link>{" "}
      |{" "}
      <Link className="App-link" to="/about">
        About
      </Link>{" "}
      |{" "}
      <Link className="App-link" to="/add">
        Add
      </Link>
    </header>
  );
}

// Header Style
const headerStyle = {
  background: "#333",
  color: "white",
  textAlign: "center",
  padding: "5px",
};
