import React from "react";
import Task from "./Task";

const Tasks = (props) => {
  if (!props.tasks || props.tasks.length < 1) {
    return null;
  }

  return props.tasks.map((task) => (
    <div key={task.id}>
      <Task task={task} alterTask={props.alterTask} delTask={props.delTask} />
    </div>
  ));
};

export default Tasks;
