import React, { useState } from "react";
import "./Form.css";

const Detail = (props) => {
  const task = props.task;
  const [deadline, setDeadline] = useState(task.deadline);
  const [importance, setImportance] = useState(task.importance);
  const [status, setStatus] = useState(task.status);
  const [description, setDescription] = useState(task.description);
  const [editPanel, setEditPanel] = useState(false);

  const { alterTask, collapse } = props;

  const detailStyle = () => {
    return {
      background: "rgba(95, 222, 253, 0.856)",
      position: "fixed",
      width: "100%",
      maxWidth: "100%",
      top: editPanel ? "15vh" : "25vh",
      margin: "none",
      paddingTop: "1%",
      paddingBottom: "1%",
      overflowY: "scroll",
    };
  };

  // Edit the Task
  const edit = () => {
    task.deadline = deadline;
    task.importance = importance;
    task.status = status;
    task.description = description;
    alterTask(task);
    collapse();
  };

  // Editing Area
  const editArea = () => {
    const imp = ["Not Important", "Important", "Very Important"];
    const st = ["To Do", "Doing", "Done", "Delayed"];
    return (
      <div className="editPanel">
        <div className="importance">
          {imp.map((i) => (
            <span key={i}>
              <button className="btn" onClick={() => setImportance(i)}>
                {i}
              </button>
            </span>
          ))}
        </div>
        <br />
        <div className="status">
          {st.map((i) => (
            <span key={i}>
              <button className="btn" onClick={() => setStatus(i)}>
                {i}
              </button>
            </span>
          ))}
        </div>
        <br />
        <input
          type="date"
          onChange={(event) => setDeadline(event.target.value)}
        />
        <br />
        <input
          type="text"
          value={description}
          onChange={(event) => setDescription(event.target.value)}
        />
        <br />
        <button className="fullBtn" onClick={edit}>
          Submit
        </button>
      </div>
    );
  };

  return (
    <div style={detailStyle()}>
      <button className="btn" onClick={collapse}>
        X
      </button>
      <h3 className="titleStyle">{task.title}</h3>
      <div className="content">
        <span className="heads">Deadline</span>
        <span className="info">
          {task.deadline ? task.deadline : "No Deadline"}
        </span>
      </div>

      <div className="content">
        <span className="heads">Importance</span>
        <span className="info">{importance}</span>
      </div>
      <div className="content">
        <span className="heads">Status</span>
        <span className="info">{status}</span>
      </div>
      <p>{description ? description : null}</p>
      <br />
      <button className="fullBtn" onClick={() => setEditPanel(!editPanel)}>
        Edit
      </button>
      <br />
      {editPanel ? editArea() : null}
    </div>
  );
};

export default Detail;
