import React, { useState } from "react";
import { useHistory } from "react-router";
import { v4 as uuid } from "uuid";
import "./layout/Form.css";

const Form = (props) => {
  const [title, setTitle] = useState("");
  const [deadline, setDeadline] = useState("");
  const [importance, setImportance] = useState("Important");
  const [status, setStatus] = useState("To Do");
  const [description, setDescription] = useState("");

  const detailStyle = () => {
    return {
      background: "rgba(95, 222, 253, 0.856)",
      position: "fixed",
      width: "100%",
      maxWidth: "100%",
      top: "20vh",
      margin: "none",
      paddingTop: "1%",
      paddingBottom: "1%",
      overflowY: "scroll",
    };
  };

  const { alterTask } = props;
  const history = useHistory();

  // Close the Form
  const collapse = () => {
    history.push("/");
  };

  // Add the Task
  const add = () => {
    const task = {
      id: uuid(),
      title: title,
      deadline: deadline,
      importance: importance,
      status: status,
      description: description,
    };
    alterTask(task);
    console.log("The Task thats added: ", task);
    collapse();
  };

  // Form Area
  const FormArea = () => {
    const imp = ["Not Important", "Important", "Very Important"];
    const st = ["To Do", "Doing", "Done", "Delayed"];
    return (
      <div className="editPanel">
        <input
          type="text"
          value={title}
          placeholder="Enter Title"
          onChange={(event) => setTitle(event.target.value)}
          required
        />
        <br />
        <br />
        <div className="importance">
          {imp.map((i) => (
            <span key={i}>
              <button className="btn" onClick={() => setImportance(i)}>
                {i}
              </button>
            </span>
          ))}
        </div>
        <br />
        <div className="status">
          {st.map((i) => (
            <span key={i}>
              <button className="btn" onClick={() => setStatus(i)}>
                {i}
              </button>
            </span>
          ))}
        </div>
        <br />
        <input
          type="date"
          onChange={(event) => setDeadline(event.target.value)}
        />
        <br />
        <br />
        <input
          type="text"
          value={description}
          onChange={(event) => setDescription(event.target.value)}
        />
        <br />
        <br />
        <button className="fullBtn" onClick={add}>
          Submit
        </button>
      </div>
    );
  };

  return (
    <div style={detailStyle()}>
      <button className="btn" onClick={collapse}>
        X
      </button>
      <hr />

      <h3 className="titleStyle">{title ? title : "Title"}</h3>
      <div className="content">
        <span className="heads">Deadline</span>
        <span className="info">{deadline ? deadline : "No Deadline"}</span>
      </div>

      <div className="content">
        <span className="heads">Importance</span>
        <span className="info">{importance}</span>
      </div>
      <div className="content">
        <span className="heads">Status</span>
        <span className="info">{status}</span>
      </div>
      <p>{description ? description : null}</p>
      <br />
      <hr />
      <br />
      {FormArea()}
    </div>
  );
};

export default Form;
