import React from "react";
import ImpBar from "../layout/ImpBar";
import StatusBar from "../layout/StatusBar";
import Tasks from "../Tasks";

const Home = (props) => {
  return (
    <div>
      <ImpBar />
      <Tasks
        tasks={props.tasks}
        alterTask={props.alterTask}
        delTask={props.delTask}
      />
      <StatusBar />
    </div>
  );
};

export default Home;
